import DashboardOutlined from "@mui/icons-material/DashboardOutlined";
import GridViewOutlined from "@mui/icons-material/GridViewOutlined";
import { Box, Card, Container, Typography } from "@mui/material";
import { Outlet, useLocation } from "react-router";
import { useTask } from "../../../../Application";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import ResponsiveTabBar from "../../../../components/ResponsiveTabBar";
import { TabView } from "../../../../tabs";
import useNavigateTask from "../../../../utils/useNavigateTask";

const tour = {
    key: 'manage-cases',
    steps: [{
        title: "مدیریت پرونده‌ها",
        intro: "",
    }, {
        title: "پرونده جدید",
        intro: "با کلیک برروی این دکمه و ورود مشخصات پرونده می‌توانید پرونده جدیدی به لیست پرونده‌ها اضافه کنید.",
        element: ".add-new",
    }]
};

const Boards = () => {

    const location = useLocation();

    const navigateTask = useNavigateTask();


    const taskHome = useTask('Tasks.home');
    const taskList = useTask('Tasks.list');


    return (
        <TabView {...taskHome} tour={tour}>
            <ResponsiveContainer fullHeight>
                <Container disableGutters>
                    <Card variant="outlined" sx={{ flexGrow: 1, display: 'flex', flexDirection: 'column' }}>

                        <Box sx={{
                            display: "flex",
                            alignItems: 'start',
                            justifyContent: 'start',
                            p: { xs: 2, sm: 3 }
                        }}
                        >
                            <Box sx={{ flexGrow: 1, display: 'flex', alignItems: 'center' }}>
                                <Box sx={{ flexGrow: 1 }}>
                                    <Typography variant="h5" sx={{ fontWeight: 600 }}>
                                        مدیریت وظایف
                                    </Typography>
                                </Box>
                            </Box>
                        </Box>
                        <ResponsiveTabBar
                            tabs={[
                                {
                                    label: 'پیشخوان',
                                    value: `/tasks`,
                                    icon: <DashboardOutlined />,
                                    onClick: () => navigateTask(taskHome)
                                },
                                {
                                    label: 'بردهای وظایف',
                                    value: `/tasks/boards`,
                                    icon: <GridViewOutlined />,
                                    onClick: () => navigateTask(taskList)
                                },
                            ]}
                            value={location.pathname}
                        >
                            <Outlet />
                        </ResponsiveTabBar>
                    </Card>
                </Container>
            </ResponsiveContainer>
        </TabView>
    )
}


export default Boards