import CloseIcon from "@mui/icons-material/CloseOutlined";
import MenuIcon from "@mui/icons-material/MenuOutlined";
import { Box, DialogTitle, Divider, Grid, IconButton, List, ListItem, ListItemButton, ListItemIcon, ListItemText, Tab, Tabs, alpha, styled, useMediaQuery, useTheme } from "@mui/material";
import { useNavigate } from "react-router";
import Sticky from 'react-stickynode';
import useLocationHash from "../utils/useLocationHash";
import BottomSheet from "./BottomSheet";
import useHideOnScroll from "../utils/useHideOnScroll";
import { useState } from "react";


const TabsContainer = styled(Box)(({ theme }) => ({
    display: 'flex',
    background: '#fff',
}))

const StyledTabs = styled((props) => (
    <Tabs
        {...props}
        TabIndicatorProps={{ children: <span className="MuiTabs-indicatorSpan" /> }}
    />
))(({ theme }) => ({
    //
}));

const StyledTab = styled((props) => <Tab disableRipple {...props} />, {
    shouldForwardProp: prop => prop !== 'isUnderTabBar'
})(
    ({ theme, isUnderTabBar }) => ({
        transition: theme.transitions.create('all'),
        borderTopLeftRadius: !isUnderTabBar && theme.shape.borderRadius * 3,
        borderTopRightRadius: !isUnderTabBar && theme.shape.borderRadius * 3,
        '&.Mui-selected': {
            backgroundColor: alpha(theme.palette.primary.main, 0.1)
        },
    }));


const TabList = ({ tabs, value }) => {
    return (
        <List>
            {tabs.map((tab, index) => (
                <ListItem disablePadding divider key={index}>
                    <ListItemButton onClick={tab.onClick} selected={value === tab.value}>
                        <ListItemIcon>
                            {tab.icon}
                        </ListItemIcon>
                        <ListItemText primary={tab.label} />
                    </ListItemButton>
                </ListItem>
            ))}
        </List>
    )
}


const ResponsiveTabBar = ({
    tabs,
    children,
    value
}) => {

    const theme = useTheme()
    const isResponsive = useMediaQuery(theme.breakpoints.down('sm'));

    const navigate = useNavigate();
    const segmentName = 'more';
    const { hasSegment, addSegment, removeSegment } = useLocationHash();

    const [isSticky, setIsSticky] = useState(false);
    const isTabBarHidden = useHideOnScroll();

    const handleStateChange = status => setIsSticky(status.status === Sticky.STATUS_FIXED);
    const isNavStickedUnderTabBar = () => !isTabBarHidden && isSticky;


    return (
        <Box>
            {!(!isResponsive && tabs.length > 10) && (
                <Sticky
                    top={isTabBarHidden ? 48 : 48 * 2}
                    innerZ={1200}
                    onStateChange={handleStateChange}
                >
                    <>
                        <TabsContainer>
                            <StyledTabs
                                variant="scrollable"
                                scrollButtons="auto"
                                // allowScrollButtonsMobile
                                value={value}
                                sx={{ flexGrow: 1, minHeight: 'unset' }}
                            >
                                {tabs.slice(0, isResponsive ? 5 : 10).map((tab, index) => (
                                    <StyledTab
                                        isUnderTabBar={isNavStickedUnderTabBar()}
                                        iconPosition="start"
                                        sx={{ minHeight: 'unset' }}
                                        key={index}
                                        {...tab}
                                    />
                                ))}
                            </StyledTabs>
                            {isResponsive && tabs.length > 5 ? (
                                <IconButton
                                    sx={{ minWidth: '48px' }}
                                    onClick={() => navigate(addSegment(segmentName))}
                                >
                                    <MenuIcon />
                                </IconButton>
                            ) : null}
                        </TabsContainer>
                        <Divider sx={{ mt: '-1px' }} />
                    </>
                </Sticky>
            )}
            {!isResponsive && tabs.length > 10 ? (
                <>
                    <Divider />
                    <Grid container>
                        <Grid item sm={3}>
                            <TabList tabs={tabs} value={value} />
                        </Grid>
                        <Divider orientation="vertical" sx={{ mr: '-1px' }} flexItem />
                        <Grid item sm={9}>
                            {children}
                        </Grid>
                    </Grid>
                </>
            ) : children}
            {isResponsive && tabs.length > 5 ? (
                <BottomSheet
                    open={hasSegment(segmentName)}
                    onClose={() => navigate(removeSegment(segmentName), { replace: true })}
                >
                    <DialogTitle sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                        <Box>گزینه‌های بیشتر</Box>
                        <IconButton onClick={() => navigate(removeSegment(segmentName), { replace: true })} size="small">
                            <CloseIcon />
                        </IconButton>
                    </DialogTitle>
                    <Divider />
                    <TabList tabs={tabs} value={value} />
                </BottomSheet>
            ) : null}
        </Box>
    )
}

ResponsiveTabBar.propTypes = {

}

export default ResponsiveTabBar;